.card-div{
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  background-color: white;
  border-radius: 10px;
}

.rightside-div{
  width: 50%;
}
